<template>
  <div class="w-full flex items-center">
    <div class="w-[50px] text-center py-0.5 bg-white border-b border-slate-300">
      <span class="text-xs">
        {{ calculatedPosition }}
      </span>
    </div>
    <div
      class="flex-1 px-2 py-0.5 bg-white flex align-center border-b border-l border-slate-300 items-center space-x-1"
    >
      <span v-if="leaderboardStore.status.type.completed && position < 3"
        ><trophy-icon
          class="h-3 w-3"
          :class="[
            { 'text-amber-400': position === 0 },
            { 'text-grey-400': position === 1 },
            { 'text-amber-700': position === 2 },
          ]"
      /></span>
      <span>
        {{ model.teamName }}
      </span>
    </div>
    <div
      class="w-[54px] py-0.5 flex justify-center items-center border-b text-tournament-200"
      :class="[
        {
          'bg-tournament-300 border-tournament-300': model.totalScore < 0,
        },
        {
          'bg-tournament-900 border-tournament-900':
            model.totalScore > -1 || displayScore === 'CUT',
        },
      ]"
    >
      <span>
        {{ displayScore }}
      </span>
    </div>
  </div>
</template>

<script setup>
import { computed } from "vue";
import { useLeaderboardStore } from "@/stores/leaderboard";
import { TrophyIcon } from "@heroicons/vue/24/solid";

const leaderboardStore = useLeaderboardStore();

const props = defineProps({
  model: {
    type: Object,
    default: () => ({}),
  },
  position: {
    type: Number,
    default: null,
  },
});

const displayScore = computed(() => {
  if (props.model.totalScore === 0) {
    return "E";
  } else {
    return props.model.totalScore;
  }
});

const calculatedPosition = computed(() => {
  let rank = leaderboardStore.leaderboard.findIndex(
    (team) => team.totalScore === props.model.totalScore
  );

  if (
    leaderboardStore.leaderboard[rank + 1]?.totalScore ===
    props.model.totalScore
  ) {
    return `T${rank + 1}`;
  } else {
    return rank + 1;
  }
});
</script>
